	module.exports = {"data":{"markdownRemark":{"html":"<p>In early 2012 I joined the teams working on the newest Cerner platform, Millennium+.\nIt was an ambitious re-architecture of Cerner's electronic medical record platform to enable new types of applications.</p>\n<p>I worked on application services that enabled semantic search of different areas of a patient's chart.\nThis involved building the search indexes, writing batch and real-time processing jobs to index this data, and writing services to make the data available to applications.</p>\n<p>The search indexes were built on Solr, and the services built with Java and Groovy using <a href=\"https://thrift.apache.org\">Apache Thrift</a> for a protocol.</p>","fields":{"project":{"title":"Cerner Millennium+","subtitle":"A new platform for electronic medical software","status":"contracted","createdAtTimestamp":"2012-01-01T00:00:00.000Z","createdAt":"January 2012","deployedUrl":"https://www.cerner.com/solutions/powerchart-touch","endedAtTimestamp":"2013-01-31T00:00:00.000Z","endedAt":"January 2013","sourceUrl":null}}},"site":{"siteMetadata":{"title":"Adam Stegman"}}},"pathContext":{"slug":"/projects/cerner-millennium-plus"}}

